#crystalOrbLogo {
  transform-origin: center;
}

.crystalBallClickable {
  max-width: 35em;
  width: 100%;
  min-width: 20em;
  height: auto;

  .cb-stars {
    fill: $color-white;
  }
}

#findIssue {
  font-weight: bold;
  font-size: 3em;
  margin-bottom: 2.5em;
}

.crystalBallContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 2;
}

.fortuneContainer {
  position: absolute;
  left: 10%;
  transform: translateY(6em);
  font-size: 0.8em;
  background-color: transparent;
  border: 0.1em solid $color-secondary;
  border-radius: 0.3em;
  width: 80%;
  max-height: 15em;
  overflow: hidden;

  footer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .fortuneBox {
    text-align: center;
    display: flex;
    flex-direction: column;
    padding: 1em;
    flex: 1;
      h2, p {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
  }
}

.crystalBallIssueButton {
  padding: 1em 2em;
  left: 50%;
  margin-left: -5em;
  top: 15em;
  position: absolute;
  background-color: $color-secondary;
  color: $color-white;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 0.2em;
  z-index: 5;
}

.fade-enter-active, .fade-leave-active {
  transition: opacity 2s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>